.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
html,
body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

html.has-scroll-smooth {
    overflow: hidden;
}

[data-scroll-container] {
    min-height: 100vh;
    overflow: hidden; /* Let Locomotive handle scrolling */
    will-change: transform;
}

[data-scroll-section] {
    position: relative;
    will-change: transform;
}